export default {
  data: () => ({
    logo: require("@/assets/logo.svg"),
    mobileLogo: require("@/assets/mobileLogo.svg"),
    drawer: false,
    menu: [
      { title: " ", link: "/admin/car", icon: "edit" },
      { title: " ", link: "/admin/cars", icon: "vertical_split" },
      { title: "", link: "/admin/orders", icon: "note_add" },
      { title: "", link: "/admin/categories", icon: "grid_view" },
      { title: " ", link: "/admin/points", icon: "place" },
      { title: "", link: "/admin/rates", icon: "local_offer" },
    ],
  }),
  computed: {
    appLogo() {
      if (this.$vuetify.breakpoint.smAndDown) {
        return this.mobileLogo;
      } else {
        return this.logo;
      }
    },
  },
};
